@import url("https://fonts.googleapis.com/css?family=Red+Hat+Display|Red+Hat+Text&display=swap");
html,
body {
  margin: 0;
  font-family: 'Rex Hat Text', sans-serif;
  color: #f5f8fa;
  scroll-behavior: smooth; }

article,
aside,
footer,
header,
nav,
section,
main {
  display: block; }

* {
  box-sizing: border-box;
  font-family: inherit; }

*:before,
*:after {
  box-sizing: inherit; }

a,
a:visited,
a:hover,
a:active {
  text-decoration: none;
  color: #48aff0; }
  a:hover,
  a:visited:hover,
  a:hover:hover,
  a:active:hover {
    text-decoration: underline; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  font-family: 'Red Hat Display', sans-serif; }

[hidden] {
  display: none !important; }

[disabled] {
  cursor: not-allowed; }
